/* PrismJS 1.15.0
https://prismjs.com/download.html#themes=prism-okaidia&languages=markup+css+clike+javascript&plugins=line-numbers */
/**
 * okaidia theme for JavaScript, CSS and HTML
 * Loosely based on Monokai textmate theme by http://www.monokai.nl/
 * @author ocodia
 */

code[class*="language-"],
pre[class*="language-"] {
	color: #f8f8f2;
	background: none;
	text-shadow: 0 1px rgba(0, 0, 0, 0.3);
	font-family: Consolas, Monaco, 'Andale Mono', 'Ubuntu Mono', monospace;
	text-align: left;
	white-space: pre;
	word-spacing: normal;
	word-break: normal;
	word-wrap: normal;
	line-height: 1.5;

	-moz-tab-size: 4;
	tab-size: 4;

	-webkit-hyphens: none;
	-ms-hyphens: none;
	hyphens: none;
}

/* Code blocks */
pre[class*="language-"] {
	padding: 1em;
	margin: .5em 0;
	overflow: auto;
	border-radius: 0.3em;
}

:not(pre) > code[class*="language-"],
pre[class*="language-"] {
	background: #272822;
}

/* Inline code */
:not(pre) > code[class*="language-"] {
	padding: .1em;
	border-radius: .3em;
	white-space: normal;
}

.token.comment,
.token.prolog,
.token.doctype,
.token.cdata {
	color: slategray;
}

.token.punctuation {
	color: #f8f8f2;
}

.namespace {
	opacity: .7;
}

.token.property,
.token.tag,
.token.constant,
.token.symbol,
.token.deleted {
	color: #f92672;
}

.token.boolean,
.token.number {
	color: #ae81ff;
}

.token.selector,
.token.attr-name,
.token.string,
.token.char,
.token.builtin,
.token.inserted {
	color: #a6e22e;
}

.token.operator,
.token.entity,
.token.url,
.language-css .token.string,
.style .token.string,
.token.variable {
	color: #f8f8f2;
}

.token.atrule,
.token.attr-value,
.token.function,
.token.class-name {
	color: #e6db74;
}

.token.keyword {
	color: #66d9ef;
}

.token.regex,
.token.important {
	color: #fd971f;
}

.token.important,
.token.bold {
	font-weight: bold;
}
.token.italic {
	font-style: italic;
}

.token.entity {
	cursor: help;
}

pre[class*="language-"].line-numbers {
	position: relative;
	padding-left: 3.8em;
	counter-reset: linenumber;
}

pre[class*="language-"].line-numbers > code {
	position: relative;
	white-space: inherit;
}

.line-numbers .line-numbers-rows {
	position: absolute;
	pointer-events: none;
	top: 0;
	font-size: 100%;
	left: -3.8em;
	width: 3em; /* works for line-numbers below 1000 lines */
	letter-spacing: -1px;
	border-right: 1px solid #999;

	-webkit-user-select: none;
	-moz-user-select: none;
	-ms-user-select: none;
	user-select: none;

}

	.line-numbers-rows > span {
		pointer-events: none;
		display: block;
		counter-increment: linenumber;
	}

		.line-numbers-rows > span:before {
			content: counter(linenumber);
			color: #999;
			display: block;
			padding-right: 0.8em;
			text-align: right;
		}


@font-face {
  font-family: Inconsolata;
  font-style: normal;
  font-weight: 400;
  src: url(/offlinesandbox/static/media/Inconsolata-Bold.b0312789.ttf) format("truetype"); }

pre.Editor {
  position: absolute;
  top: 0;
  left: 0;
  margin: 0;
  border-radius: 0 !important; }

pre[class*="language-"].line-numbers.Editor {
  padding-left: 2.5em; }

pre.Editor,
pre.Editor > code {
  height: 100%;
  width: 100%;
  font-family: Inconsolata, source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace;
  font-size: 1em;
  line-height: 1;
  overflow-x: auto;
  white-space: pre;
  word-break: break-all;
  word-wrap: break-word; }

pre.Editor > textarea {
  position: absolute;
  overflow: hidden;
  box-sizing: border-box;
  border: none;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  resize: none;
  background-color: transparent;
  -webkit-text-fill-color: transparent;
  caret-color: #FFF;
  /* Inherit from parent pre */
  /* all:inherit */
  padding: inherit;
  padding-left: inherit;
  line-height: inherit;
  font-family: inherit;
  font-size: inherit;
  text-align: inherit;
  white-space: pre;
  word-spacing: inherit;
  word-break: inherit;
  word-wrap: inherit; }

#IFrame {
  position: absolute;
  top: 0;
  left: 0;
  margin: 0;
  width: 100%;
  height: 100%;
  border: none;
  outline: none; }

#Toolbar {
  position: fixed;
  bottom: 0;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  padding: 0.65rem 0; }

html, body {
  box-sizing: border-box;
  position: relative; }

*, *:before, *:after {
  box-sizing: inherit; }

html {
  height: 100%;
  width: 100%; }

body {
  min-height: 100%;
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale; }

#root {
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%; }

/* On screens that are 992px wide or less, go from four columns to two columns */
@media screen and (max-width: 1024px) {
  body, html {
    font-size: 15px; } }

/* On screens that are 600px wide or less, make the columns stack on top of each other instead of next to each other */
@media screen and (max-width: 768px) {
  body, html {
    font-size: 14px; } }

